.dt-container {
  width: 95%;
}

.dt-content {
  margin-bottom: 4rem;
}

.dt-cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  margin-top: 1rem;
  gap: 2rem;
}

.dt-card {
  width: 100%;
  min-width: 20rem;
  max-width: 25rem;
  aspect-ratio: 16/11;
  background: #232428;
  display: grid;
  grid-template-rows: 4fr 1fr;
  border-radius: 0.5rem;
  overflow: hidden;
  cursor: pointer;
  border: 1px solid transparent;
}

.dt-card:hover {
  border: 1px solid rgba(255, 255, 255, 0.5);
}

.dt-card-img {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  background: #232428;
  padding: 0.2rem;
  text-align: center;
}

.dt-card-img img {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0.6;
}

.dt-card-img h2 {
  position: absolute;
  color: white;
  z-index: 1;
  padding: 1rem;
  width: 100%;
}
.dt-card-header {
  display: flex;
  padding: 0.8rem 1rem;
  overflow: hidden;
  align-items: center;
  justify-content: center;
}

/* .dt-card-header h3 {
  box-sizing: border-box;
  font-size: 1.1rem;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
} */

.dt-card-header-details {
  display: flex;
  justify-content: center;
  gap: 0.6rem;
}

.dt-card-header-details-item {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  width: max-content;
  font-size: 0.9rem;
}

/*------------------------------Responsive------------------------------*/

@media screen and (max-width: 1700px) {
  .dt-cards {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media screen and (max-width: 1400px) {
  .dt-cards {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 1100px) {
  .dt-cards {
    grid-template-columns: 1fr;
  }
}
